section.project-welcome {
    display: flex;
    justify-content: center;
    align-items: center;
    background: url(../imgs/project-bg.png);
    background-size: cover;
    height: 400px;
    flex-direction: column;
    color: #fff !important;
    h1 {
        font-size: 2em;
    }
    p {
        a {
            color: #fff;
        }
    }
}

section.project {
    background: url(../imgs/bg-2.png);
    background-size: cover;
    position: relative;
    padding: 100px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;

    &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: #e8d5c493;
        z-index: 1;
    }
    * {
        position: relative;
        z-index: 2;
    }
    .img {
        margin-bottom: 40px;
        padding: 25px 40px;
        background-color: #fff;

        h2 {
            font-weight: 500;
            font-size: 2em;
            margin-bottom: 40px;
        }
        img {
            max-width: 100%;
        }
    }
    .about {
        padding: 20px;
        display: block;
        background-color: #fff;
        color: #333;
        h3 {
            font-size: 1.5em;
        }
        h4 {
            font-size: 1.3em;
            margin-bottom: 30px;
        }
        ul {
            margin: 50px 0;
            p {
                font-weight:700;
                span {
                    font-weight: 400;
                }
            }
        }
        .imgs {
            display: flex;
            flex-wrap: wrap;
            gap: 40px;
            img {
                max-width: calc(100% / 3 - 40px);
                border: 1px solid #dfdfdf;
                border-radius: 3px;
                cursor: pointer;
            }
        }
        .another {
            margin-top: 50px;
            .cards {
                display: flex;
                flex-wrap: wrap;
                gap: 30px;
                .card {
                    .text {
                        padding: 0;
                    }
                    .img {
                        padding: 0;
                        background-image: none;
                    }
                }
            }
        }
    }
}