footer {
    padding-top: 30px;
    padding-bottom: 70px;
    display: flex;
    gap: 20px;
    img {
        max-width: 18%;
    }
    > p {
        width: 30%;
        margin-top: 10px;
    }
    p {
        font-size: 14px;
        color: #333;
    }
    h3 {
        color: var(--main-color);
        margin-bottom: 10px;
    }
    ul {
        display: flex;
        gap: 10px;
        i {
            display: inline-block;
            height: fit-content;
            padding: 10px;
            background-color: #365b4921;
            border-radius: 4px;
            color: var(--main-color);
            transition: .3s;
            &:hover {
                color: var(--icon-color);
            }
        }
    }
    .copywrite {
        position: absolute;
        bottom: 0;
        left: 0;
        padding: 15px;
        background-color: #faf8f9;
        text-align: center;
        width: 100%;
        border-top: 1px solid #ebeeed ;
    }
    a {
        color: #000 !important;
        &:hover {
            color: var(--icon-color) !important;
        }
    }
}