@import url('https://fonts.googleapis.com/css2?family=Cairo:wght@400;600;700;800&display=swap');

:root {
    --main-color: #365b48;
    --seconde-color : #bda588;
}
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
ul {
     list-style: none;
}
a {
     text-decoration: none;
}
html {
    scroll-behavior: smooth;
}
body {
    direction: rtl;
    font-family: 'Cairo', sans-serif;
    position: relative;
}
.padding-60 {
    padding-left: 60px !important;
    padding-right: 60px !important;
}
.btn {
    background-color: var(--seconde-color);
    padding: 10px 25px;
    border-radius: 25px;
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    transition: .3s;
    &:hover {
        background-color: #fff;
        color: var(--seconde-color);
    }
}
/* Start Banner */

.social {
    a {
        color: #fff;
        font-size: 14px;
        margin-left: 10px;
        transition: .3s;
        
        &:hover {
            color: var(--icon-color);
        }
    }

}
/* End Banner */

/* Start Welcome */
section.welcome {
    min-height: calc(100vh - 40px);
    background: url(imgs/bg.jpg);
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0,0,.7);
        z-index: 1;
    }
    > * {
        position: relative;
        z-index: 2;
    }
    .text {
        h1 {
            font-size: 4em;
            color: #fff;
        }
        h3 {
            font-size: 2em;
            color: var(--seconde-color);
        }
        p {
            color: #eee;
            max-width: 80%;
            font-size: 18px;
        }
    }
    form {
        background-image: url(imgs/form-bg.jpg);
        background-size: cover;
        background-repeat: no-repeat;
        display: flex;
        flex-direction: column;
        gap: 20px;
        padding: 40px 30px;
        width: 30%;
        border-radius: 6px;
        h2 {
            color: #fff;
            font-size: 1.7em;
        }
        p {
            font-size: 14px !important;
        }
        > *{
            direction: rtl;
            font-family: 'Cairo', sans-serif;
        }
        input,select {
            padding: 10px 15px ;
            background: #fff;
            border-radius: 4px;
            cursor: pointer;
            font-size: 14px;
            outline: none;
            border: none;
        }
        button {
            cursor: pointer;
            border: none;
        }
    }
}
/* End Welcome */
/* Start About */
.about {
    display: flex;
    .img {
        background-image: url(imgs/about-bg.jpg);
        background-size: cover;
        direction: ltr;
        padding-top: 70px;
        padding-left: 30px;
        img {
            max-width: 90%;
            border-radius: 6px;
        }
    }
    .text {
        padding-top: 70px;
        padding-right: 20px;
        padding-left: 60px;
        h2 {
            color: var(--seconde-color);
            font-weight: bold;
        }
        > p {
            font-size: 14px;
            line-height: 2;
        } 
        .boxes {
            margin-top: 40px;
            .box {
                margin-bottom: 20px;
                display: flex;
                gap: 10px;
                i {
                    background: var(--seconde-color);
                    font-size: 1.5em;
                    padding: 10px;
                    height: fit-content;
                    color: #fff;
                    border-radius: 4px;
                }
                h3 {
                    color: #333;
                    font-weight: 900;
                }
                p {
                    font-size: 14px;
                    color: #111;
                }
            }
        }
    }
}
/* End About */
/* Strat Services */
.services {
    padding: 100px 60px;
    background: url(imgs/form-bg.jpg);
    background-size: cover;
    text-align: center;
    color: #fff;
    h2 {
        font-size: 2em;
    }
    .boxes {
        margin-top: 40px;
        display: flex;
        gap: 20px;
        .box {
            position: relative;
            background: #fff; 
            padding: 20px;
            display: flex;
            flex-direction: column;
            align-items: center;
            img {   
                max-width: 80%;
                margin-bottom: 20px;
                transition: .5s;
            }
            span {
                color: #333;
                font-weight: 900;
            }
            &::before,
            &::after {
                content: '';
                position: absolute;
                background-color: var(--seconde-color);
                width: 0%;
                height: 3px;
                transition: 1s;
            }
            &::before {
                top: 0;
                left: 0;
            }
            &::after {
                right: 0;
                bottom: 0;
            }
            /* Hover */
            &:hover {
                img {
                    transform: rotateY(360deg);
                }
                span {
                    color: var(--seconde-color);
                }
                &::before,
                &::after {
                    width: 50%;
                }
            }
        }
    }
}
/* End Services */
/* Start Projects */
section.projects {
    padding: 100px 60px;
    background-color: var(--seconde-color);
    background: url(imgs/bg-2.png);
    background-size: cover;
    position: relative;
    text-align: center;
    color: var(--main-color);
    h2 {
        font-size: 2em;
    }
    .cards {
        display: flex;
        flex-wrap: wrap;
        gap: 30px;
        padding-bottom: 100px;
        margin-top: 40px;
        .card {
            position: relative;
            overflow: hidden;
            width: calc(100% / 3 - 40px);
            border-radius: 6px;
            background-color: #fff;
            transition: .3s;
            .text {
                text-align: right;
            }
            .img {
                position: relative;
                img {
                    max-width: 100%;
                    transition: .3s;
                }
                .link {
                    padding: 0;
                    margin: 0;
                    position: absolute;
                    height: 102%;
                    left: 0;
                    top: 0%;
                    width: 100%;
                    background: #057b0545;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-size: 6em;
                    transform: translateY(-100%);
                    z-index: 500;
                    transition: .3s;
                }
            }
            a {
                padding-right: 20px;
                color: #333;
                font-size: 1.2em;
                margin-top: 40px;
                display: block;
            }
            p {
                padding-right: 20px;
                color: #777;
                i {
                    color: var(--seconde-color);
                }
            }
            ul {
                margin-top: 20px;
                border-top: 1px solid #eee;
                display: flex;
                li {
                    width: calc(100% / 3);
                    text-align: center;
                    font-size: 14px;
                    padding: 10px;
                    &:not(:last-child) {
                        border-left: 1px solid #eee;
                    }
                }
            }
            .handeler {
                position: absolute;
                left: 15px;
                top: 10px;
                padding: 3px 17px;
                z-index: 20;
                background-color: rgba(0, 0, 0, 0.5);
                border-radius: 2px;
                font-size: 12px;
                color: #fff;
                transition: .3s;
            }
            &:hover {
                box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
                transform: translateY(-5px);
                img {
                    transform: scale(1.1);
                }
                .handeler {
                    background-color: #fff;
                    color: #3f56ff;
                }
                .link {
                    transform: translateY(0%);
                }
            }
        }
    }
    &::before {
        content: '';
        background-color: #e8d5c493;
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
    }
    * {
        z-index: 2;
        position: relative;
    }
} 
/* End Projects */
/* Start Benifits */
section.benifits {
    text-align: center;
    padding:70px ;
    h2 {
        color: var(--main-color);
        font-size: 2em;
    }
    .cards {
        margin-top: 40px;
        display: flex;
        gap: 30px;
        .card {
            width: calc(100% / 3 - 30px);
            border-radius: 10px;
            position: relative;
            overflow: hidden;
            transition: .3s;
            img {
                max-width: 100%;
                height: 100%;
                transition: .3s;
            }
            .text {
                position: absolute;
                bottom: 10%;
                z-index: 3;
                text-align: right;
                color: #fff;
                padding-right: 20px;

                transition: .3s;
                h4 {
                    font-size: 1.5em;
                }
                p {
                    color: #eee;
                    width: 80%;
                }
            }
            &::before {
                content: '';
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                background: linear-gradient(180deg,rgba(15,15,15,0),rgba(15,15,15,.75));
                z-index: 2;
                transition: .3s;
            }
            &:hover {
                img {
                    transform: scale(1.1);
                }
                &::before {
                    background: linear-gradient(2deg, #2e5043 8%, #c7c7c700 100%);
                }
                .text {
                    transform: translateY(-50px);
                }
            }
        }
    }
}
/* End Benifits */
/* Start Contact */
section.contact {
    padding: 100px 0;
    
    background: url(imgs/form-bg.jpg);
    background-size: cover;
    text-align: center;
    color: #fff;
    h2 {
        font-size: 2.5em;
    }
    p {
        font-size: 18px;
        margin-bottom: 50px;
    }
    div {
        display: flex;
        gap: 10px;
        justify-content: center;
    }
    a {
        border-radius: 4px;
        padding: 20px 45px;
        color: #fff;
        font-size: 18px;
        font-weight: bold;
        border: 1px solid var(--seconde-color);
        transition: .3s;
        &:hover {
            background-color: #fff;
            color: var(--seconde-color);
        }
        &:last-child {
            border: 1px solid #fff;
            &:hover {
                border-color: var(--seconde-color);
                background-color: var(--seconde-color);
                color: #fff;
            }
        }
    }
}
/* End Contact */
@media (max-width:767px) {
    .padding-60 {
        padding-left: 30px;
        padding-right: 30px;
    }
    header,
    .welcome,
    .about ,
    .boxes,
    .cards,
    footer,
    .links {
        flex-direction: column;
    }
    .links {
        gap: 10px !important;
        margin-top: 30px;
    }
    footer {
        text-align: center;
        align-items: center;
        img {
            max-width: 50%;
        }
        p {
            width: 100%;
        }
    }
    .welcome {
        padding-top: 230px;
        padding-bottom: 50px;
        form {
            width: 100% !important;
            margin-top: 30px;
        }
    }
    .about .img {
        padding-right: 10px;
        direction: rtl;
    }
    .card {
        width: 100% !important;
    }
    .contact .btn{
        padding: 10px 20px;
    }
}