.banner {
    display: flex;
    justify-content: space-between;
    height: 40px;
    display: flex;
    align-items: center;
    background: var(--main-color);
    .icons {
        a.icon {
            position: relative;
            color: #fff;
            font-size: .9em;
            transition: .3s;
            &::before {
                content: '';
                position: absolute;
                bottom: -5px;
                right: 0;
                width: 0%;
                height: 2px;
                border-radius: 20px;
                background-color: var(--seconde-color);
                transition: .3s;
            }
            &:hover::before {
                width: 100%;
            }
            &:last-child {
                margin-right: 10px;
            }
            i {
                color: var(--seconde-color);
            }
        }
    }
}
/* Start Header */
header {
    position: fixed;
    top: 50px;
    left: 0;
    width: 100%;
    padding: 40px 0;
    z-index: 100;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: .3s;
    > img {
        max-width: 100px;
    }
    ul {
        display: flex;
        gap: 15px;
        a {
            color: #fff;
            transition: .3s;
            &:hover {
                color: var(--seconde-color);
            }
        }
    }
    .links {    
        display: flex;
        gap: 30px;
        align-items: center;
    }
    &.scrolled {
        top: 0;
        background-color: #fff;
        ul {
            a {
                color: #333;
                &.active,
                &:hover {
                    color: var(--seconde-color);
                }
            }
        }
    }
}
/* End Header */
svg {
    width:13px;
    margin-left: 5px; 
    fill : var(--seconde-color);
}