.card {
    position: relative;
    overflow: hidden;
    width: calc(100% / 3 - 40px);
    border-radius: 6px;
    background-color: #fff;
    transition: .3s;
    .text {
        text-align: right;
    }
    .img {
        position: relative;
        img {
            max-width: 100%;
            transition: .3s;
        }
        .link {
            padding: 0;
            margin: 0;
            position: absolute;
            height: 102%;
            left: 0;
            top: 0%;
            width: 100%;
            background: #057b0545;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 6em;
            transform: translateY(-100%);
            z-index: 500;
            transition: .3s;
        }
    }
    a {
        padding-right: 20px;
        color: #333;
        font-size: 1.2em;
        margin-top: 40px;
        display: block;
    }
    p {
        padding-right: 20px;
        color: #777;
        i {
            color: var(--seconde-color);
        }
    }
    ul {
        margin-top: 20px;
        border-top: 1px solid #eee;
        display: flex;
        li {
            width: calc(100% / 3);
            text-align: center;
            font-size: 14px;
            padding: 10px;
            &:not(:last-child) {
                border-left: 1px solid #eee;
            }
        }
    }
    .handeler {
        position: absolute;
        left: 15px;
        top: 10px;
        padding: 3px 17px;
        z-index: 20;
        background-color: rgba(0, 0, 0, 0.5);
        border-radius: 2px;
        font-size: 12px;
        color: #fff;
        transition: .3s;
    }
    &:hover {
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
        transform: translateY(-5px);
        img {
            transform: scale(1.1);
        }
        .handeler {
            background-color: #fff;
            color: #3f56ff;
        }
        .link {
            transform: translateY(0%);
        }
    }
}